
import React, { useState } from 'react';

const Main = () => {
  const [paymentAmount, setPaymentAmount] = useState(100000);
  const [paymentDescription, setPaymentDescription] = useState('Pixel');

  const handlePayment = async () => {
    try {
      const paymentData = {
        amount: paymentAmount,
        description: paymentDescription,
        payload: 'your_custom_data_here' //  ,  
      };

      //   Telegram API   
      const response = await fetch('https://api.telegram.org/bot6456444469:AAEdSkn3w2SQcxkJUISClVsvPJWXvfciPnk/sendInvoice', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          chat_id: '5668476910',
          title: '',
          description: paymentData.description,
          payload: paymentData.payload,
          provider_token: '1744374395:TEST:77662926122e91cd7020',
          currency: 'RUB', //  
          prices: [{ label: '', amount: paymentData.amount * 100 }] //   
        })
      });

      const responseData = await response.json();
      console.log(responseData);
    } catch (error) {
      console.error('    :', error);
    }
  };

  return (
    <div>
      <h1>-    Telegram</h1>
      <label>
          ( ):
        <input type="number" value={paymentAmount} onChange={(e) => setPaymentAmount(e.target.value)} />
      </label>
      <br />
      <label>
         :
        <input type="text" value={paymentDescription} onChange={(e) => setPaymentDescription(e.target.value)} />
      </label>
      <br />
      <button onClick={handlePayment} className='text-white'> </button>
    </div>
  );
};

export default Main;

























