import React, { useState, useEffect } from "react";
import { Routes, Route } from "react-router-dom";
import Main from "./pages/Main";
import WebApp from "@twa-dev/sdk";
import { TonConnectUIProvider } from "@tonconnect/ui-react";
import Wallet from "./pages/Wallet";
import Rewards from "./pages/Rewards";
import appState from "./store/store";
import langpack from "./utils/langpack";
import Loader from "./components/Loader";

function App() {
  useEffect(() => {
    WebApp.setHeaderColor("#000");
    WebApp.setBackgroundColor("#000");
    WebApp.expand();
  }, []);

  // const [userId, setUserId] = useState(WebApp.initDataUnsafe.user.id);
  const [userId, setUserId] = useState(1011);

  const [loading, setLoading] = useState(true);

  useEffect(() => {
    setTimeout(() => {
      setLoading(false);
    }, 1500);
  }, []);

  const [balance, setBalance] = useState(() => {
    const savedBalance = localStorage.getItem("balance");
    return savedBalance ? JSON.parse(savedBalance) : 0;
  });

  const updateBalance = (newBalance) => {
    setBalance(newBalance);
    localStorage.setItem("balance", JSON.stringify(newBalance));
  };

  const [oldBalance, setOldBalance] = useState(0);

  const updateOldBalance = (newOldBalance) => {
    setOldBalance(newOldBalance);
  };

  const [bet, setBet] = useState(10);

  const updateBet = (newBet) => {
    setBet(parseInt(newBet));
  };

  var bodyElement = document.body;
  let lang;
  try {
    lang = WebApp.initDataUnsafe.user.language_code;
  } catch {
    lang = "en";
  }
  // const lang = WebApp.initDataUnsafe.user.language_code;
  const platform = WebApp.platform;
  if (platform === "ios" || platform === "macos") {
    bodyElement.classList.add(`apple`);
  } else {
    if (platform !== "unknown") {
      bodyElement.classList.add(`material`);
    } else {
      bodyElement.classList.add(`material`);
    }
  }

  const result = langpack(lang);

  useEffect(() => {
    const pixelLangpack = {
      main: {
        bet_amount: result.main.bet_amount,
        min: result.main.min,
        max: result.main.max,
        profit: result.main.profit,
      },
      wallet: {
        deposit: result.wallet.deposit,
        withdraw: result.wallet.withdraw,
        select_currency: result.wallet.select_currency,
        bank_card: result.wallet.bank_card,
        bank_card_desc: result.wallet.bank_card_desc,
        purchase: result.wallet.purchase,
        deposit_blockchain: result.wallet.deposit_blockchain,
        deposit_blockchain_desc: result.wallet.deposit_blockchain_desc,
        withdraw_blockchain: result.wallet.withdraw_blockchain,
        withdraw_blockchain_desc: result.wallet.withdraw_blockchain_desc,
        deposit_amount: result.wallet.deposit_amount,
        withdrawal_amount: result.wallet.withdrawal_amount,
        available: result.wallet.available,
        pay: result.wallet.pay,
        minimal_amount: result.wallet.minimal_amount,
        connect_wallet: result.wallet.connect_wallet,
      },
      rewards: {
        rewards: result.rewards.reward,
        your_ref: result.rewards.your_ref,
        bonus_px: result.rewards.bonus_px,
        share: result.rewards.share,
        claimable_earnings: result.rewards.claimable_earnings,
        claim_earnings: result.rewards.claim_earnings,
      },
    };

    //     JSON
    const jsonString = JSON.stringify(pixelLangpack);

    //   JSON  sessionStorage
    sessionStorage.setItem("pixelLangpack", jsonString);
  });

  return (
    <>
      <TonConnectUIProvider manifestUrl="https://satshimoto.github.io/cdn/tonconnect-manifest.json">
        <Routes>
          {loading ? (
            <Route path="/" Component={Loader} />
          ) : (
            <>
              <Route
                path="/"
                element={
                  <Main
                    balance={balance}
                    updateBalance={updateBalance}
                    bet={bet}
                    updateBet={updateBet}
                  />
                }
              />
              <Route
                path="/wallet"
                element={
                  <Wallet balance={balance} updateBalance={updateBalance} />
                }
              />
              <Route path="/rewards" element={<Rewards userId={userId} />} />
            </>
          )}
        </Routes>
      </TonConnectUIProvider>
    </>
  );
}

export default App;
