import { useState, useEffect } from "react";
import { TonConnectUIProvider, TonConnectButton } from "@tonconnect/ui-react";
import { useNavigate } from "react-router-dom";
import { BackButton } from "@twa-dev/sdk/react";
import appState from "../../store/store";
import { action } from "mobx";
import { useTonConnectUI } from "@tonconnect/ui-react";
import transformToNanotons from "../../utils/transformToNanotons";
import WebApp from "@twa-dev/sdk";

const Wallet = action(({ balance, updateBalance }) => {
  const navigate = useNavigate();
  const [pagination, setPagination] = useState(0);
  const updatePagination = (newPagination) => {
    setPagination(newPagination);
  };
  const [asset, setAsset] = useState("deposit");

  const updateAsset = (newAsset) => {
    setAsset(newAsset);
  };

  // const [wallet, setWallet] = useState(false);

  // useEffect(() => {
  //   setWallet(
  //     localStorage.getItem("ton-connect-ui_wallet-info") ? true : false
  //   );
  // }, []);

  const [wallet, setWallet] = useState(false);

  useEffect(() => {
    const updateWalletState = () => {
      setWallet(
        localStorage.getItem("ton-connect-ui_wallet-info") ? true : false
      );
    };

    //    
    const intervalId = setInterval(updateWalletState, 1000);

    //       
    const handleStorageChange = () => {
      updateWalletState();
    };
    window.addEventListener("storage", handleStorageChange);

    //        
    return () => {
      clearInterval(intervalId);
      window.removeEventListener("storage", handleStorageChange);
    };
  }, []);

  const [error, setError] = useState(false);

  const [inputId, setInputId] = useState(undefined);

  const [val, setVal] = useState("");
  const onChange = (e) => {
    clearTimeout(inputId);
    const { value } = e.target;
    setError(false);
    if (parseInt(value.replaceAll(",", "")) === 0 && val === "") {
      return;
    }
    if (parseInt(value.replaceAll(",", "")) >= 100001) {
      return;
    }
    if (parseInt(value.replaceAll(",", "")) > 10000) {
      setInputId(
        setTimeout(() => {
          setError(true);
        }, 1000)
      );
    }

    e.preventDefault();
    setVal(value);
  };
  const onKeyDown = (e) => {
    if (e.shiftKey === true) {
      if (e.which == 9) {
        return true;
      }
      e.preventDefault();
    }
    if (e.which > 57) {
      e.preventDefault();
    }
    if (e.which == 32) {
      e.preventDefault();
    }
    return true;
  };
  const formatValue = (value) => {
    return value
      .toString()
      .replaceAll(",", "")
      .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,");
  };

  const withdraw = () => {
    if (isNaN(parseInt(val.replaceAll(",", "")))) {
      return;
    }
    if (parseInt(val.replaceAll(",", "")) < 2000) {
      return;
    }
    if (parseInt(val.replaceAll(",", "")) > balance) {
      return;
    }
    updateBalance(balance - parseInt(val.replaceAll(",", "")));
    navigate("/");
  };

  const transaction = {
    messages: [
      {
        address: "UQBlGR3ho4ZS9MUbLimmjM03Zh5DmCKl5HD0RpZrbyPbLSKl", // destination address
        // amount: `${(parseInt(val.replaceAll(",", "")) / 1000)
        //   .toString()
        //   .replace(".", "")}000000000`, // 10 Toncoin  
        amount: transformToNanotons(parseInt(val.replaceAll(",", "")) / 1000),
      },
    ],
  };
  const [tonConnectUI, setOptions] = useTonConnectUI();

  const deposit = async () => {
    if (isNaN(parseInt(val.replaceAll(",", "")))) {
      return;
    }
    if (parseInt(val.replaceAll(",", "")) < 1000) {
      return;
    }
    try {
      await tonConnectUI.sendTransaction(transaction).then(() => {
        updateBalance(balance + parseInt(val.replaceAll(",", "")));
        navigate("/");
      });
    } catch (error) {
      console.error("  :", error);
    }
  };

  const savedData = sessionStorage.getItem("pixelLangpack");
  const parsedData = JSON.parse(savedData);

  return (
    <>
      <BackButton onClick={() => navigate("/")} />
      <div className="page scrolling">
        <div className="container relative">
          <div className="page__content mb-[150px]">
            <div className="flex justify-center items-center">
              <div className="text-white text-[18px] font-semibold p-[20px]">
                <TonConnectButton />
              </div>
            </div>
            <div className="px-[20px]">
              <div
                className="h-[1px] w-full"
                style={{ background: "rgba(255, 255, 255, 0.1)" }}
              ></div>
            </div>
            <div className="px-[14px] pb-[14px]">
              <div className="LibrarySegmentedControl LibrarySegmentedControl--state-fixed LibrarySegmentedControl--glider">
                <div
                  onClick={() => updateAsset("deposit")}
                  className={
                    asset === "deposit"
                      ? `LibrarySegmentedControlItem__link LibrarySegmentedControlItem__link--active`
                      : `LibrarySegmentedControlItem__link`
                  }
                >
                  <div
                    className={
                      asset === "deposit"
                        ? `LibrarySegmentedControlItem LibrarySegmentedControlItem--active`
                        : `LibrarySegmentedControlItem`
                    }
                  >
                    <div className="flex items-center">
                      <div>
                        <svg
                          width="18px"
                          height="18px"
                          viewBox="0 0 24 24"
                          role="img"
                          xmlns="http://www.w3.org/2000/svg"
                          aria-labelledby="arrowUpIconTitle"
                          stroke={asset === "withdraw" ? "#aaa" : "#fff"}
                          stroke-width="2"
                          stroke-linecap="round"
                          stroke-linejoin="round"
                          fill="none"
                          color="#fff"
                          className="mr-[6px] transform rotate-[180deg]"
                        >
                          <script xmlns="" />{" "}
                          <title id="arrowUpIconTitle">Arrow Up</title>{" "}
                          <path d="M9 10.5l3-3 3 3" /> <path d="M12 16.5V9" />{" "}
                          <path stroke-linecap="round" d="M12 7.5V9" />{" "}
                          <circle cx="12" cy="12" r="10" />{" "}
                        </svg>
                      </div>
                      <div className="Typography LibrarySegmentedControlItem__inner LibraryLabel LibraryLabel--l-1 LibraryLabel--w-semi-bold">
                        {parsedData.wallet.deposit}
                      </div>
                    </div>
                  </div>
                </div>
                <div
                  onClick={() => updateAsset("withdraw")}
                  className={
                    asset === "withdraw"
                      ? `LibrarySegmentedControlItem__link LibrarySegmentedControlItem__link--active`
                      : `LibrarySegmentedControlItem__link`
                  }
                >
                  <div
                    className={
                      asset === "withdraw"
                        ? `LibrarySegmentedControlItem LibrarySegmentedControlItem--active`
                        : `LibrarySegmentedControlItem`
                    }
                  >
                    <div className="flex items-center">
                      <div>
                        <svg
                          width="18px"
                          height="18px"
                          viewBox="0 0 24 24"
                          role="img"
                          xmlns="http://www.w3.org/2000/svg"
                          aria-labelledby="arrowUpIconTitle"
                          stroke={asset === "deposit" ? "#aaa" : "#fff"}
                          stroke-width="2"
                          stroke-linecap="round"
                          stroke-linejoin="round"
                          fill="none"
                          color="#fff"
                          className="mr-[6px]"
                        >
                          <path d="M9 10.5l3-3 3 3" /> <path d="M12 16.5V9" />{" "}
                          <path stroke-linecap="round" d="M12 7.5V9" />{" "}
                          <circle cx="12" cy="12" r="10" />{" "}
                        </svg>
                      </div>
                      <div className="Typography LibrarySegmentedControlItem__inner LibraryLabel LibraryLabel--l-1 LibraryLabel--w-semi-bold">
                        {parsedData.wallet.withdraw}
                      </div>
                    </div>
                  </div>
                </div>
                <div
                  className="LibrarySegmentedControl__glider"
                  style={{
                    width: "calc(50% - 6px)",
                    transform:
                      asset === "deposit"
                        ? "translateX(calc(0% + 0px))"
                        : "translateX(calc(100% + 4px))",
                  }}
                ></div>
              </div>
            </div>
            {asset === "deposit" ? (
              <section>
                <div className="pt-[14px] px-[14px]">
                  <div className="flex justify-start items-center">
                    <div className="text-[14px] text-[#9092b2]">
                      {parsedData.wallet.select_currency}
                    </div>
                  </div>
                </div>
                <div className="px-[14px]">
                  <div className="flex items-center bg-[#15171e] w-full h-[40px] rounded-lg p-[8px]">
                    <div>
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        width="46"
                        height="46"
                        fill="none"
                        viewBox="0 0 40 40"
                        class="block rounded-full h-[20px] w-[20px] mr-[8px]"
                      >
                        <rect
                          width="40"
                          height="40"
                          fill="#0098EA"
                          rx="20"
                        ></rect>
                        <path
                          fill="#fff"
                          d="M26.831 11H13.173c-2.512 0-4.103 2.709-2.84 4.899l8.43 14.61a1.43 1.43 0 0 0 2.478 0l8.431-14.61c1.262-2.187-.33-4.899-2.84-4.899zm-8.075 15.128-1.836-3.553-4.43-7.922a.774.774 0 0 1 .68-1.157h5.584V26.13zm8.754-11.477-4.428 7.926-1.836 3.551V13.494h5.583c.612 0 .973.65.68 1.157z"
                        ></path>
                      </svg>
                    </div>
                    <div className="font-semibold text-white text-[14px]">
                      TON
                    </div>
                  </div>
                </div>
                <div className="p-[14px] mt-[14px]">
                  <div
                    class="w-full h-[1px]"
                    style={{
                      background: "rgb(37, 42, 53)",
                    }}
                  ></div>
                </div>
                <div className="p-[14px]">
                  <div class="flex flex-col gap-2">
                    <h4 class="flex flex-col justify-start font-medium gap-2 text-white">
                      <div className="">
                        <div className="flex items-center gap-2">
                          <svg
                            xmlns="http://www.w3.org/2000/svg"
                            aria-hidden="true"
                            role="img"
                            class="text-[yellow] iconify iconify--whale"
                            width="0.89em"
                            height="1em"
                            viewBox="0 0 16 18"
                          >
                            <path
                              fill="currentColor"
                              stroke="currentColor"
                              stroke-linecap="round"
                              stroke-linejoin="round"
                              d="M8.833.667 1.411 9.573c-.29.349-.436.523-.438.67a.42.42 0 00.154.33c.115.093.342.093.796.093H8l-.834 6.667 7.422-8.906c.291-.35.437-.524.439-.67a.42.42 0 00-.155-.331c-.115-.093-.342-.093-.796-.093H8z"
                            ></path>
                          </svg>{" "}
                          {parsedData.wallet.bank_card}
                        </div>
                      </div>
                      <div className="flex flex-row gap-4">
                        <div className="flex items-center gap-2">
                          <img
                            src="https://www.svgrepo.com/show/367195/sberbank.svg"
                            alt=""
                            className="h-[16px] w-[16px]"
                          />{" "}
                          <div className="text-[#aaa] text-[14px]">Sberbank</div>
                        </div>
                        <div className="flex items-center gap-2">
                          <img
                            src="https://asset.brandfetch.io/id7rfMRhdn/idCb99vlfX.png"
                            alt=""
                            className="h-[16px] w-[16px]"
                          />{" "}
                          <div className="text-[#aaa] text-[14px]">Tinkoff</div>
                        </div>
                        <div className="flex items-center gap-2">
                          <img
                            src="https://online.raiffeisen.ru/favicon-1024.png"
                            alt=""
                            className="h-[16px] w-[16px]"
                          />{" "}
                          <div className="text-[#aaa] text-[14px]">Raiffeisen</div>
                        </div>
                      </div>
                    </h4>{" "}
                    <p
                      class="text-sm"
                      style={{
                        color: "rgb(144, 146, 178)",
                      }}
                    >
                      {parsedData.wallet.bank_card_desc}
                    </p>{" "}
                    <div class="flex flex-row gap-4">
                      <button
                        // onClick={() =>
                        //   WebApp.openTelegramLink(
                        //     "https://t.me/wallet?startattach=market"
                        //   )
                        // }
                        onClick={() =>
                          WebApp.openTelegramLink("https://t.me/BitcFort")
                        }
                        type="button"
                        tabindex="0"
                        class="inline-flex items-center justify-center text-sm font-medium whitespace-nowrap ring-offset-background transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:pointer-events-none disabled:opacity-50 bg-gradient-primary text-white border-1 border-brandblue-300 border-solid active:opacity-90 xxs:h-9 xxs:min-w-9 sm:h-11 sm:min-w-11 rounded-lg px-8 w-full"
                        data-button-root=""
                      >
                        {" "}
                        {parsedData.wallet.purchase}
                      </button>{" "}
                    </div>
                  </div>
                </div>
                <div className="p-[14px] mt-[14px]">
                  <div
                    class="w-full h-[1px]"
                    style={{
                      background: "rgb(37, 42, 53)",
                    }}
                  ></div>
                </div>
                <div className="p-[14px]">
                  <div className="flex">
                    <div>
                      <svg
                        class="h-[21px] w-[21px] mr-[8px]"
                        xmlns="http://www.w3.org/2000/svg"
                        fill="currentColor"
                      >
                        <path
                          fillRule="evenodd"
                          clipRule="evenodd"
                          d="M15.0002 3.5C12.471 3.5 10.3406 5.20717 9.6983 7.53206C13.2833 7.86295 16.1373 10.7169 16.4682 14.3019C18.7931 13.6597 20.5002 11.5292 20.5002 9C20.5002 5.96243 18.0378 3.5 15.0002 3.5ZM14.5 15C14.5 11.9624 12.0376 9.5 9 9.5C5.96243 9.5 3.5 11.9624 3.5 15C3.5 18.0376 5.96243 20.5 9 20.5C12.0376 20.5 14.5 18.0376 14.5 15ZM6.5 15L9 17.5L11.5 15L9 12.5L6.5 15Z"
                          fill="#aaa"
                        ></path>
                      </svg>
                    </div>
                    <div className="text-white font-semibold">
                      {parsedData.wallet.deposit_blockchain}
                    </div>
                  </div>
                  <div className="text-[#9092b2] text-left text-[14px]">
                    {parsedData.wallet.deposit_blockchain_desc}
                  </div>
                </div>
                <div className="px-[14px] pt-[14px]">
                  <div className="flex justify-between items-center">
                    <div className="text-[#9092b2] text-[14px]">
                      {parsedData.wallet.deposit_amount}
                    </div>
                    <div className="flex">
                      <div className="text-[#9092b2] text-[14px]">
                        {parsedData.wallet.pay}:
                      </div>
                      <div className="text-[#fff] text-[14px] ml-[4px]">
                        {val ? parseInt(val.replaceAll(",", "")) / 1000 : 0} TON
                      </div>
                    </div>
                  </div>
                </div>
                <div class="flex flex-row w-full gap-2 justify-center items-center px-[14px]">
                  <div class="w-full h-full relative">
                    <div className="flex items-center bg-[#15171e] w-full h-[40px] rounded-lg p-[8px]">
                      <div className="">
                        <img
                          src="https://agafouov.github.io/cdn/pixelcoin.jpg"
                          alt=""
                          className="block rounded-full h-[22px] w-[22px] mr-[8px]"
                        />
                      </div>
                      <input
                        className="outline-none bg-[#15171e] text-[#fff] font-bold shrink-0"
                        inputMode="decimal"
                        onKeyDown={onKeyDown}
                        onChange={onChange}
                        value={formatValue(val)}
                        placeholder="0"
                      />
                    </div>
                  </div>
                </div>
                <div className="px-[14px]">
                  <div className="flex justify-start items-center">
                    <div className="text-[#9092b2] text-[14px]">
                      {parsedData.wallet.minimal_amount}:
                    </div>
                    <div>
                      <img
                        src="https://agafouov.github.io/cdn/pixelcoin.jpg"
                        alt=""
                        className="block rounded-full h-[18px] w-[18px] ml-[4px]"
                      />
                    </div>
                    <div className="text-[#fff] text-[14px] ml-[4px]">
                      1,000 PX
                    </div>
                  </div>
                </div>
                <div className="px-[14px] mt-[24px]">
                  {wallet ? (
                    <button
                      onClick={() => deposit()}
                      // className={`flex items-center justify-center bg-[#0098EA] w-full h-[40px] rounded-lg p-[14px] ${
                      //   (isNaN(parseInt(val.replaceAll(",", ""))) ||
                      //     parseInt(val.replaceAll(",", "")) < 1000) &&
                      //   `opacity-[0.3]`
                      // }`}
                      className={`${
                        (isNaN(parseInt(val.replaceAll(",", ""))) ||
                          parseInt(val.replaceAll(",", "")) < 1000) &&
                        `opacity-[0.3] pointer-events-none`
                      } inline-flex items-center justify-center text-sm font-medium whitespace-nowrap ring-offset-background transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:pointer-events-none disabled:opacity-50 bg-gradient-primary text-white border-1 border-brandblue-300 border-solid active:opacity-90 xxs:h-9 xxs:min-w-9 sm:h-11 sm:min-w-11 rounded-lg px-8 w-full gap-1 mt-2 mb-2`}
                    >
                      <div>
                        <svg
                          width="18px"
                          height="18px"
                          viewBox="0 0 24 24"
                          role="img"
                          xmlns="http://www.w3.org/2000/svg"
                          aria-labelledby="arrowUpIconTitle"
                          stroke={"#fff"}
                          stroke-width="2"
                          stroke-linecap="round"
                          stroke-linejoin="round"
                          fill="none"
                          color="#fff"
                          className="mr-[6px] transform rotate-[180deg]"
                        >
                          <path d="M9 10.5l3-3 3 3" /> <path d="M12 16.5V9" />
                          <path stroke-linecap="round" d="M12 7.5V9" />
                          <circle cx="12" cy="12" r="10" />
                        </svg>
                      </div>
                      <div className="font-semibold text-white">
                        {parsedData.wallet.deposit}
                      </div>
                    </button>
                  ) : (
                    <button
                      // className={`flex items-center justify-center bg-[#0098EA] w-full h-[40px] rounded-lg p-[14px] opacity-[0.3]
                      // `}
                      className="opacity-[0.3] pointer-events-none inline-flex items-center justify-center text-sm font-medium whitespace-nowrap ring-offset-background transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:pointer-events-none disabled:opacity-50 bg-gradient-primary text-white border-1 border-brandblue-300 border-solid active:opacity-90 xxs:h-9 xxs:min-w-9 sm:h-11 sm:min-w-11 rounded-lg px-8 w-full gap-1 mt-2 mb-2"
                    >
                      <div className="font-semibold text-white">
                        {parsedData.wallet.connect_wallet}
                      </div>
                    </button>
                  )}
                </div>
              </section>
            ) : (
              <section>
                <div className="pt-[14px] px-[14px]">
                  <div className="flex justify-start items-center">
                    <div className="text-[#9092b2] text-[14px]">
                      {parsedData.wallet.select_currency}
                    </div>
                  </div>
                </div>
                <div className="px-[14px]">
                  <div className="flex items-center bg-[#15171e] w-full h-[40px] rounded-lg p-[8px]">
                    <div>
                      <img
                        src="https://agafouov.github.io/cdn/pixelcoin.jpg"
                        alt=""
                        className="block rounded-full h-[22px] w-[22px] mr-[8px]"
                      />
                    </div>
                    <div className="font-semibold text-white">PX</div>
                  </div>
                </div>
                <div className="p-[14px]">
                  <div className="flex">
                    <div>
                      <svg
                        class="h-[21px] w-[21px] mr-[8px]"
                        xmlns="http://www.w3.org/2000/svg"
                        fill="currentColor"
                      >
                        <path
                          fillRule="evenodd"
                          clipRule="evenodd"
                          d="M15.0002 3.5C12.471 3.5 10.3406 5.20717 9.6983 7.53206C13.2833 7.86295 16.1373 10.7169 16.4682 14.3019C18.7931 13.6597 20.5002 11.5292 20.5002 9C20.5002 5.96243 18.0378 3.5 15.0002 3.5ZM14.5 15C14.5 11.9624 12.0376 9.5 9 9.5C5.96243 9.5 3.5 11.9624 3.5 15C3.5 18.0376 5.96243 20.5 9 20.5C12.0376 20.5 14.5 18.0376 14.5 15ZM6.5 15L9 17.5L11.5 15L9 12.5L6.5 15Z"
                          fill="#aaa"
                        ></path>
                      </svg>
                    </div>
                    <div className="text-white font-semibold">
                      {parsedData.wallet.withdraw_blockchain}
                    </div>
                  </div>
                  <div className="text-[#9092b2] text-left text-[14px]">
                    {parsedData.wallet.withdraw_blockchain_desc}
                  </div>
                </div>
                <div className="px-[14px] pt-[14px]">
                  <div className="flex justify-between items-center">
                    <div className="text-[#9092b2] text-[14px]">
                      {parsedData.wallet.withdrawal_amount}
                    </div>
                    <div className="flex">
                      <div className="text-[#9092b2] text-[14px]">
                        {parsedData.wallet.available}:
                      </div>
                      <div className="text-[#fff] text-[14px] ml-[4px]">
                        {formatValue(balance)} PX
                      </div>
                    </div>
                  </div>
                </div>
                <div class="flex flex-row w-full gap-2 justify-center items-center px-[14px]">
                  <div class="w-full h-full relative">
                    <div className="flex items-center bg-[#15171e] w-full h-[40px] rounded-lg p-[8px]">
                      <div className="">
                        <img
                          src="https://agafouov.github.io/cdn/pixelcoin.jpg"
                          alt=""
                          className="block rounded-full h-[22px] w-[22px] mr-[8px]"
                        />
                      </div>
                      <input
                        className="outline-none bg-[#15171e] text-[#fff] font-bold shrink-0"
                        inputMode="decimal"
                        onKeyDown={onKeyDown}
                        onChange={onChange}
                        value={formatValue(val)}
                        placeholder="0"
                      />
                    </div>
                  </div>
                </div>
                <div className="px-[14px]">
                  <div className="flex justify-start items-center">
                    <div className="text-[#9092b2] text-[14px]">
                      {parsedData.wallet.minimal_amount}
                    </div>
                    <div>
                      <img
                        src="https://agafouov.github.io/cdn/pixelcoin.jpg"
                        alt=""
                        className="block rounded-full h-[18px] w-[18px] ml-[4px]"
                      />
                    </div>
                    <div className="text-[#fff] text-[14px] ml-[4px]">
                      2,000 PX
                    </div>
                  </div>
                </div>
                <div className="px-[14px] mt-[24px]">
                  {wallet ? (
                    <button
                      onClick={() => withdraw()}
                      // className={`flex items-center justify-center bg-[#0098EA] w-full h-[40px] rounded-lg p-[14px] ${
                      //   (isNaN(parseInt(val.replaceAll(",", ""))) ||
                      //     parseInt(val.replaceAll(",", "")) < 2000 ||
                      //     parseInt(val.replaceAll(",", "")) > balance) &&
                      //   `opacity-[0.3]`
                      // }`}
                      className={`${
                        (isNaN(parseInt(val.replaceAll(",", ""))) ||
                          parseInt(val.replaceAll(",", "")) < 2000 ||
                          parseInt(val.replaceAll(",", "")) > balance) &&
                        `opacity-[0.3] pointer-events-none`
                      } inline-flex items-center justify-center text-sm font-medium whitespace-nowrap ring-offset-background transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:pointer-events-none disabled:opacity-50 bg-gradient-primary text-white border-1 border-brandblue-300 border-solid active:opacity-90 xxs:h-9 xxs:min-w-9 sm:h-11 sm:min-w-11 rounded-lg px-8 w-full gap-1 mt-2 mb-2`}
                    >
                      <div>
                        <svg
                          width="18px"
                          height="18px"
                          viewBox="0 0 24 24"
                          role="img"
                          xmlns="http://www.w3.org/2000/svg"
                          aria-labelledby="arrowUpIconTitle"
                          stroke={"#fff"}
                          stroke-width="2"
                          stroke-linecap="round"
                          stroke-linejoin="round"
                          fill="none"
                          color="#fff"
                          className="mr-[6px] transform rotate-[180deg]"
                        >
                          <path d="M9 10.5l3-3 3 3" /> <path d="M12 16.5V9" />
                          <path stroke-linecap="round" d="M12 7.5V9" />
                          <circle cx="12" cy="12" r="10" />
                        </svg>
                      </div>
                      <div className="font-semibold text-white">
                        {parsedData.wallet.withdraw}
                      </div>
                    </button>
                  ) : (
                    <button
                      // className={`flex items-center justify-center bg-[#0098EA] w-full h-[40px] rounded-lg p-[14px] opacity-[0.3]
                      // `}
                      className="opacity-[0.3] pointer-events-none inline-flex items-center justify-center text-sm font-medium whitespace-nowrap ring-offset-background transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:pointer-events-none disabled:opacity-50 bg-gradient-primary text-white border-1 border-brandblue-300 border-solid active:opacity-90 xxs:h-9 xxs:min-w-9 sm:h-11 sm:min-w-11 rounded-lg px-8 w-full gap-1 mt-2 mb-2"
                    >
                      <div className="font-semibold text-white">
                        {parsedData.wallet.connect_wallet}
                      </div>
                    </button>
                  )}
                </div>
              </section>
            )}
          </div>
        </div>
      </div>
    </>
  );
});

export default Wallet;
